.header {
  width: 100%;
  background-color: #3939ec;
  justify-content: center;
  padding: 10px 0;
  display: flex;
  position: fixed;
  box-shadow: 0 2px 5px #888;
}

.search-form {
  border-radius: 0;
}

.search-form:first-child {
  margin: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-top: 60px;
  display: flex;
}

.photo-card {
  width: 300px;
  box-shadow: 0 2px 5px #888;
}

img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin: 0;
  display: block;
}

.info {
  border: 1px solid #4d4a4a;
  border-top: none;
  justify-content: center;
  gap: 12px;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 14px;
}

